.contact_info_title {
    margin-top: 3%;
    margin-left: 3%;
    margin-right: 3%;
    color: orange;
    font-family: 'Grinched';
    font-size: 3.0rem;
    text-align: center;
    text-shadow: 1px 1px 2px black;
    line-height: 0.9;
}
.contact_email_wrapper {
    width: 100%;
    min-height: 450px;
    text-align: center;
}
.contact_email_image {
    width: 40%;
    height: 100%;
    float: left;
}
.contact_email_image_img {
    width: auto;
    height: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.contact_email_image_alt_text {
    width: 100%;
    height: 20%;
    margin-top: 30px;
    padding-left: 30px;
    padding-right: 30px;
    font-family: 'Lucida Console';
    font-size: 14px;
}
.contact_email_form {
    width: 59%;
    height: 100%;
    float: right;
}
.contact_textfield {
    height: 13%;
    width: 90%;
    margin-top: 20px;
    margin-left: 5%;
    margin-right: 5%;
}
.contact_connect_wrapper {
    width: 100%;
    height: 100px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.contact_connect_wrapper img {
    height: 80px;
    width: auto;
    transition: all .7s;
}
.contact_connect_wrapper:hover img {
    transform: scale(1.05);
}

@media only screen and (max-width: 680px) {
    .contact_email_image {
        width: 30%;
    }
    .contact_email_image_img {
        width: 90%;
        height: auto;
    }
    .contact_email_image_img img {
        width: 96%;
        height: auto;
    }
    .contact_email_image_alt_text {
        width: 100%;
        height: 20%;
        margin-top: 7px;
        padding: 10px;
        font-size: 13px;
    }
    .contact_email_form {
        width: 69%;
    }
}
